<template>
  <div>
    <nav-header></nav-header>
    <nav-bread>
      <span>Goods</span>
    </nav-bread>
    <div class="accessory-result-page accessory-page">
      <div class="container">
        <div class="filter-nav">
          <span class="sortby">Sort by:</span>
          <a href="javascript:void(0)" class="default cur">Default</a>
          <a href="javascript:void(0)" class="price" @click="sortGoods">Price
            <svg class="icon icon-arrow-short" :class="{'sort-up':sortFlag}">
              <use xlink:href="#icon-arrow-short"></use>
            </svg>
          </a>
          <a href="javascript:void(0)" class="filterby stopPop" :class="{'filterby-show': filterbyShow}"
             @click="showFilterPop">Filter by</a>
        </div>
        <div class="accessory-result">
          <!-- filter -->
          <div class="filter stopPop" id="filter" :class="{'filterby-show':filterbyShow}">
            <dl class="filter-price">
              <dt>Price:</dt>
              <dd @click="setPriceFilter('all')">
                <a href="javascript:void(0)" :class="{'cur':priceRange === 'all'}">All</a>
              </dd>
              <dd v-for="(price,index) in priceFilter" :key="index">
                <a href="javascript:void(0)" @click="setPriceFilter(index)"
                   :class="{'cur':priceRange === index}">{{price.startPrice}} - {{price.endPrice}}</a>
              </dd>
            </dl>
          </div>

          <!-- search result accessories list -->
          <div class="accessory-list-wrap">
            <div class="accessory-list col-4">
              <ul>
                <li v-for="(good,index) in goodsList" :key="index">
                  <div class="pic">
                    <a href="#">
                      <img v-lazy="'http://mall.store.hunjiangyu.top/'+good.productImage" alt="">
                    </a>
                  </div>
                  <div class="main">
                    <div class="name">{{good.productName}}</div>
                    <div class="price">{{good.salePrice}}</div>
                    <div class="btn-area">
                      <a href="javascript:;" class="btn btn--m" @click="addCart(good.productId)">加入购物车</a>
                    </div>
                  </div>
                </li>
              </ul>
              <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10" class="view-more-normal" v-show="loading">
                <img src="./../assets/loading-spinning-bubbles.svg">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="md-overlay" v-show="overLayFlag" @click="closePop"></div>
    <modal :mdShow="mdShow" v-on:close="closeModal">
      <p slot="message">
        请先登录,否则无法加入到购物车中!
      </p>
      <div slot="btnGroup">
        <a class="btn btn--m" href="javascript:;" @click="mdShow = false">关闭</a>
      </div>
    </modal>
    <modal v-bind:mdShow="mdShowCart" v-on:close="closeModal">
      <p slot="message">
        <svg class="icon-status-ok">
          <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-status-ok"></use>
        </svg>
        <span>加入购物车成功!</span>
      </p>
      <div slot="btnGroup">
        <a class="btn btn--m" href="javascript:;" @click="mdShowCart = false">继续购物</a>
        <router-link class="btn btn--m btn--red" href="javascript:;" to="/cart">查看购物车</router-link>
      </div>
    </modal>
    <nav-footer></nav-footer>
  </div>
</template>

<script>
  import NavHeader from "@/components/NavHeader";
  import NavFooter from "@/components/NavFooter";
  import NavBread from "@/components/NavBread";
  import Modal from "@/components/Modal";

  export default {
    data() {
      return {
        goodsList: [],
        sortFlag: true,
        page:1,
        pageSize:8,
        busy:true,
        loading:false,
        priceFilter: [
          {
            startPrice: "0.00",
            endPrice: "100.00"
          },
          {
            startPrice: "100.00",
            endPrice: "1000.00"
          },
          {
            startPrice: "1000.00",
            endPrice: "5000.00"
          },
          {
            startPrice: "5000.00",
            endPrice: "20000.00"
          }
        ],
        priceRange: 'all',
        filterbyShow: false,
        overLayFlag: false,
        mdShow: false,
        mdShowCart:false
      };
    },
    components: {
      NavHeader,
      NavFooter,
      NavBread,
      Modal
    },
    mounted: function () {
      this.getGoodsList(false);
    },
    methods: {
      getGoodsList(flag) {
        var param = {
          page:this.page,
          pageSize:this.pageSize,
          sort:this.sortFlag?1:-1,
          priceRange: this.priceRange
        }
        this.loading = true;
        this.$axios.get("https://mall.hunjiangyu.top/goods/list",{
          params:param
        }).then(response => {
          var res = response.data;
          this.loading = false;
          if (res.status == "0") {
            if(flag){
              this.goodsList = this.goodsList.concat(res.result.list);
              if(res.result.count==0){
                this.busy=true;
              }else{
                this.busy = false;
              }
            }else{
              this.goodsList = res.result.list;
              this.busy = false;
            }
          } else {
            this.goodsList = [];
          }
        });
      },
      sortGoods() {
        this.sortFlag = !this.sortFlag;
        this.page =1;
        this.getGoodsList(false);
      },
      loadMore() {
        this.busy = true;
        setTimeout(() => {
          this.page++;
          this.getGoodsList(true);
        }, 1000);
      },
      addCart(productId) {
        this.$axios.post("https://mall.hunjiangyu.top/goods/addcart",{
          productId:productId
        }).then((response) => {
          var res = response.data;
          if(res.status === '0'){
            this.mdShowCart = true;
            this.$store.commit("updateCartCount",1);
          }else {
            this.mdShow = true;
          }
        })
      },
      setPriceFilter(index) {
        this.priceRange = index;
        this.page = 1;
        this.getGoodsList();
        this.closePop();
      },
      showFilterPop() {
        this.filterbyShow = true;
        this.overLayFlag = true;
      },
      closeModal(){
        this.mdShow = false;
        this.mdShowCart = false;
      },
      closePop() {
        this.filterbyShow = false;
        this.overLayFlag = false;
      }
    }
  };
</script>

<style scoped>
  .sort-up {


  }

</style>



